.settings {
    display: flex;
    width: 60vw;
    margin: auto;
  }
  
  .settingsWrapper {
    flex:9;
    padding: 20px;
  }
  
  .settingsTitle {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .settingsTitleUpdate {
    font-size: 2rem;
    font-weight: bolder;
    margin-bottom: 0.5rem;
    color: lightcoral;
  }
  
  .settingsTitleDelete {
    /* background-color: lightcoral; */
    color: red;
    font-size: 1rem;
    font-weight: bolder;
    cursor: pointer;
    padding: 1rem 1rem;
    border-radius: 2rem;
    border: 2px solid lightcoral;
    box-shadow: 0px 1px lightcoral;
  }

  .settingsTitleDelete:hover {
    background-color: red;
    color: white;
  }
  
  .settingsForm {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .settingsPP {
    display: flex;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  
  .settingsPP > img {
    height: 70px;
    width: 70px;
    border-radius: 20px;
    object-fit: cover;
  }
  
  .settingsPPIcon {
    width: 25px;
    height: 25px;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 50%;
    margin-left: 10px;
    color: white;
    background-color: lightcoral;
    cursor: pointer;
  }
  
  .settingsPPButton:focus {
    outline-style: none;
  }
  
  .settingsForm > label {
    font-size: 20px;
    margin-top: 20px;
  }
  
  .settingsPPInput {
    width: 30%;
  }
  
  .settingsForm > input {
    color: gray;
    margin-top: 10px;
    margin-bottom: 10px;
    height: 30px;
    border: none;
    border-bottom: 1px solid lightgray;
  }
  
  .settingsSubmitButton {
    align-self: center;
    width: 150px;
    border: none;
    border-radius: 10px;
    color: white;
    background-color: teal;
    padding: 10px;
    margin-top: 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .settingsSubmitButton:hover {
    background-color: rgb(1, 114, 114);
  }

  @media screen and (max-width: 640px) {
    .settings {
        display: flex;
        width: 100%;
        margin: auto;
      }
  }